body {
  display: flex;
  margin: 0;
  background: linear-gradient(72.61deg, #9AE5A8 0%, #4DB8A7 32.37%, #248997 56.35%, #2A5A74 77.49%, #283044 99.76%);
}

.notify {
  background-color: #EC1010;
  border-radius: 30px; 
  font-weight: 800;
  line-height: 7vh;
  text-align: center;
  color: #FFFFFF;
}

.Fila {
  background-color: #EBF5EE;
